import "core-js/modules/es.array.push.js";
export default {
  data() {
    return {
      newsletter: {
        title: '最新动态',
        // 新闻简报标题
        articles: [
        // 包含多个文章对象的数组
        {
          id: 1,
          // 文章ID
          title: '文章一',
          // 文章标题
          content: '这是文章一的内容',
          // 文章内容
          imgUrl: 'https://picsum.photos/400/200?random=1' // 文章图片的URL
        }, {
          id: 2,
          title: '文章二',
          content: '这是文章二的内容',
          imgUrl: 'https://picsum.photos/400/200?random=2'
        }, {
          id: 3,
          title: '文章三',
          content: '这是文章三的内容',
          imgUrl: 'https://picsum.photos/400/200?random=3'
        }, {
          id: 4,
          title: '文章四',
          content: '这是文章四的内容',
          imgUrl: 'https://picsum.photos/400/200?random=4'
        }]
      },
      searchText: '' // 用户在搜索框中输入的文本
    };
  },

  computed: {
    filteredArticles() {
      // 计算属性，根据搜索框中的文本过滤文章数组
      return this.newsletter.articles.filter(article => {
        return article.title.toLowerCase().includes(this.searchText.toLowerCase());
      });
    }
  },
  methods: {
    goToMainPage() {
      // 点击按钮返回主页
      this.$router.push('/');
    },
    shareToFacebook() {
      // 点击按钮分享至Facebook
      window.open('https://www.facebook.com/');
    },
    shareToInstagram() {
      // 点击按钮分享至Instagram
      window.open('https://www.instagram.com/');
    }
  }
};